html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  font-family: "Montserrat", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: calc(28 / 16);
  color: #131314;
  background-color: #ffffff;
  min-width: 1024px;
  min-height: 100%;
  margin: 0; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

a:focus {
  color: #070707; }

.header {
  width: 200px;
  height: 50px;
  margin: 0 auto;
  background-color: plum; }
